.UserTitle {
  display: flex;
}
.UserTitle .u-title-left {
  flex: 1 1;
}
.UserTitle .u-title-right {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
.UserTitle .u-title-right span {
  font-size: 48px;
  color: #87d068;
}
.UserTitle .u-userName {
  font-size: 24px;
  font-weight: 300;
  margin: 10px 0 20px 0;
}
.u-content .userDetail {
  display: flex;
}
.u-content .userDetail .u-detail-left {
  flex: 1 1;
}
.u-content .userDetail .u-detail-left .u-tips-content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}
.u-content .userDetail .u-detail-left .u-tips-content .u-tips {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border: 1px solid #eee;
  border-radius: 4px;
  font-size: 24px;
  font-weight: 600;
  height: 90px;
  width: 30%;
  margin-bottom: 20px;
}
.u-content .userDetail .u-detail-left .u-tips-content .u-tips .u-tips-title {
  font-size: 12px;
  margin-bottom: 10px;
  font-weight: normal;
}
.u-content .u-log-content {
  flex: 1 1;
  height: 500px;
  overflow: hidden;
  overflow-y: auto;
}
